<template>
  <div v-show="!this.isShow.isLDShow" id="music" class="music" @click="toggleMusic">
    <div class="cloud" :class="isPlay ? 'on' : 'off'"></div>
    <audio :src="musicURL" id="myMusic" preload="preload" loop></audio>
  </div>
</template>

<script>
export default {
  name: 'MusicPlayer',
  props: {
    isShow: {
      type: Object
    }
  },
  data () {
    return {
      musicURL: 'https://www.jiandoukeji.com/h5/2019/suningcards/share/music-sn.mp3',
      isPlay: true
    }
  },
  mounted () {
    this.audioPlay()
  },
  methods: {
    toggleMusic () {
      if (this.isPlay) {
        this.audioPause()
      } else {
        this.audioPlay()
      }
      this.isPlay = !this.isPlay
    },
    audioPlay () {
      let audio = document.getElementById('myMusic')
      audio.play()
      document.addEventListener('WeixinJSBridgeReady', function () {
        audio.play()
      }, false)
    },
    audioPause () {
      let audio = document.getElementById('myMusic')
      audio.pause()
      document.addEventListener('WeixinJSBridgeReady', function () {
        audio.pause()
      }, false)
    }
  }
}
</script>

<style lang="less" scoped>
.music {
  position: absolute;
  right: .5rem;
  top: .5rem;
  z-index: 3;
  margin-left: 1rem;
  margin-bottom: 1rem;
  width: 1.75rem /* 28/16 */;
  height: 2.125rem /* 34/16 */;
  .cloud {
    width: 3.125/2rem /* 50/16 */;
    height: 3.125/2rem /* 50/16 */;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .on {
    background-image: url('./music-on.png');
    animation: rotate-music 2s linear infinite;
  }
  .off {
    background-image: url('./music-off.png');
  }
  @keyframes rotate-music {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
</style>
