      //地球 + 地图动图
      this.sequenceFun(progress,'bgContainer/mapImgSprContainer/mapImgSpr',0.325248,0.01,mapImgUrl)

      //数字增长
      this.textSequenceFun(progress,'bgContainer/text1',0.279733,0.018,31217678901,43440000000,',')
      this.textSequenceFun(progress,'bgContainer/text2',0.279733,0.018,58129850,77530000,',')

      this.textSequenceFun(progress,'bgContainer/text3',0.378792,0.01,2500,9000,'')
      
