    yuyinAction(progress){
      if((progress>=0.0725)&&(progress<=0.0726)){
        if(this.yuyinFlag){
          this.yuyinPlay()
        }
      }else{
        this.yuyinFlag=true
      }

      //招聘按钮
      if(progress>0.9083186){
        $('.zhaopinBtn').show()
        $('.zhaopinBtn').addClass('tada')
      }else{
        $('.zhaopinBtn').hide()
        $('.zhaopinBtn').removeClass('tada')
      }
    },
    gameLoop(delta){
      //定时刷新
      //流星滑动
      for(let i1=3;i1<=8;i1++){
        let liuxingSpeed=this.randomCt(3,10)
        let liuxing0=this.app.stage.getChildByName('bgContainer').getChildByName('p1ImgContain').getChildAt(i1)
        liuxing0.vx=-liuxingSpeed
        liuxing0.vy=liuxingSpeed
        liuxing0.x+=liuxing0.vx
        liuxing0.y+=liuxing0.vy

        if(liuxing0.x<-80){
          liuxing0.x=750
          liuxing0.y=-this.randomCt(10,500)
        }
      }
    },
    addTxt(sprGroup,words,wordsStyle,sprName,cssStyle){
      let textCSS={
          'dropShadow': true,
          'dropShadowBlur': 18,
          'dropShadowColor': "#00b4ff",
          'dropShadowDistance': 0,
          'fontFamily': "苹方",
          'fill': "#f8f8f8",
          'fillGradientType': 1,
          'fontSize': 26,
          'fontWeight': 400,
          'lineHeight': 48,
          'letterSpacing':5,
      }

      let cssStyleArr=JSON.stringify(cssStyle)
      cssStyleArr=JSON.parse(cssStyleArr)

      for(var i in cssStyleArr){
        textCSS[i]=cssStyleArr[i]
      }

      let txtStyle = new PIXI.TextStyle(textCSS);

      

      let sprites=this.backSpr(sprGroup)
      let text=new PIXI.Text(words,txtStyle) 
      text.x=wordsStyle.x
      text.y=wordsStyle.y
      text.name=sprName
      if(wordsStyle.alpha==undefined){
        text.alpha=0.8
      }else{
        text.alpha=wordsStyle.alpha
      }
      sprites.addChild(text)
    },
    backSpr(sprGroup){
      let sprArr=sprGroup.split('/')
      let sprites=this.app.stage.getChildByName(sprArr[0])
      let sprArrNum=sprArr.length

      if(sprArrNum>1){
        for(let i=1;i<sprArrNum;i++){
          let midName=sprArr[i]
          sprites=sprites.getChildByName(midName)
        }
      }

      return sprites
    }, 
    animatedSpriteFun(sprGroup,imgUrlArr,rectangle,positionStyle,speed,animationName){
      let sprites=this.backSpr(sprGroup)

      let imgNum=imgUrlArr.length
      let frameArray=[]
      for(let i=0;i<imgNum;i++){
        let texture=new PIXI.Texture.from(imgUrlArr[i])
        let rectanglerect=new PIXI.Rectangle(rectangle.x,rectangle.y,rectangle.width,rectangle.height)
        let frame=new PIXI.Texture(texture,rectanglerect)
        frameArray.push(frame)
      }
      let myMovie=new PIXI.AnimatedSprite(frameArray)
      myMovie.position.set(positionStyle.x,positionStyle.y)
      myMovie.animationSpeed=speed
      myMovie.name=animationName
      myMovie.play()
      sprites.addChild(myMovie)
    },
    tweenFun(sprGroup,position,startTime,durTime,tweenEffect,changeJson1,changeJson2){
      let sprites=this.backSpr(sprGroup)

      let timeline=new TimelineMax({delay:startTime})
      let tween=null
      
      if(position=='position'){
        sprites=sprites.position
      }

      if(position=='scale'){
        sprites=sprites.scale
      }

      if(tweenEffect=='to'){
        tween=TweenMax.to(sprites,durTime,changeJson1)
      }else if(tweenEffect=='from'){
        tween=TweenMax.from(sprites,durTime,changeJson1)
      }else{
        tween=TweenMax.from(sprites,durTime,changeJson1,changeJson2)
      }

      timeline.add(tween,0)
      this.alltimeline.add(timeline,0)
    },
    tweenIndexFun(sprGroup,index,position,startTime,durTime,tweenEffect,changeJson1,changeJson2){
      let sprites=this.backSpr(sprGroup)
      sprites=sprites.getChildAt(index)

      let timeline=new TimelineMax({delay:startTime})
      let tween=null
      
      if(position=='position'){
        sprites=sprites.position
      }

      if(position=='scale'){
        sprites=sprites.scale
      }

      if(tweenEffect=='to'){
        tween=TweenMax.to(sprites,durTime,changeJson1)
      }else if(tweenEffect=='from'){
        tween=TweenMax.from(sprites,durTime,changeJson1)
      }else{
        tween=TweenMax.from(sprites,durTime,changeJson1,changeJson2)
      }

      timeline.add(tween,0)
      this.alltimeline.add(timeline,0)
    },
    sequenceFun(progress,sprGroup,startTime,durTime,imgUrlArr){
      let sprites=this.backSpr(sprGroup)

      if(progress>=startTime){
        let sprNum=imgUrlArr.length
        let index=Math.floor((progress-startTime)/durTime*sprNum)
        if((index>=0)&&(index<sprNum)){
          sprites.texture=new PIXI.Texture.from(imgUrlArr[index])
        }
      }
    },
    textSequenceFun(progress,sprGroup,startTime,durTime,startNum,endNum,cut){
      let sprites=this.backSpr(sprGroup)
      let txt=startNum

      let sprNum=endNum-startNum
      let currentNum=Math.floor((progress-startTime)/durTime*sprNum)+startNum
      if((currentNum>=startNum)&&(currentNum<=endNum)){
        txt=currentNum
      }else if(currentNum<startNum){
        txt=startNum
      }else{
        txt=endNum
      }

      if(cut==','){
        sprites.text=this.readNum(txt)
      }else{
        sprites.text=txt
      }

    },
    readNum(n){
      let per=3
      let m=n.toString()
      let num=m.length
      let arr=[]
      let shang=parseInt(num/per)
      let yu=num%per

      if(yu<=0){
        for(let i=0;i<shang;i++){
          let start=per*i
          arr[i]=m.substr(start,per)
        }
      }else if(yu>0){
        for(let i2=0;i2<=shang;i2++){
          let start=per*i2
          if(i2==0){
            arr[i2]=m.substr(start,yu)
          }else{

            arr[i2]=m.substr(start-(3-yu),per)
          }
        }
      }


      let backstr=arr.join(',')
      
      return backstr


    },
    randomCt(lower,upper){
      return Math.floor(Math.random()*(upper-lower+1))+lower
    },
    addGifFun(sprGroup,containeStyle,sprName,imgUrl,startIndex,containerName){
      let sprites=this.backSpr(sprGroup)

      let container=new PIXI.Container()
      container.name=containerName
      container.position.set(containeStyle.position.x,containeStyle.position.y)
      sprites.addChild(container)

      
      let spr=new PIXI.Sprite.from(imgUrl[startIndex])
      spr.width=containeStyle.width
      spr.height=containeStyle.height
      spr.name=sprName
      if(containeStyle.alpha!=undefined){
        spr.alpha=containeStyle.alpha
      }
      spr.position.set(0,0)
      container.addChild(spr)
    },
    addContainerSpritesFun(sprGroup,containeStyle,containerName,imgUrl,imgSize,imgPos){
      let sprites=this.backSpr(sprGroup)

      let container=new PIXI.Container()
      if(containerName!=''){
        container.name=containerName
      }

      if(containeStyle.width!=undefined){
        container.width=containeStyle.width
        container.height=containeStyle.height
      }
      container.position.set(containeStyle.position.x,containeStyle.position.y)
      sprites.addChild(container)

      let imgUrlNum=imgUrl.length
      for(let i=0;i<imgUrlNum;i++){
        let spr=new PIXI.Sprite.from(imgUrl[i])
        spr.width=imgSize[i][0]
        spr.height=imgSize[i][1]
        spr.position.set(imgPos[i][0],imgPos[i][1])
        container.addChild(spr)
      }
    },
    yuyinPlay () {
      this.yuyinFlag=false
      let audio = document.getElementById('yuyin')
      audio.play()
      document.addEventListener('WeixinJSBridgeReady', function () {
        audio.play()
      }, false)
    },
    yuyinPause () {
      let audio = document.getElementById('yuyin')
      audio.pause()
      document.addEventListener('WeixinJSBridgeReady', function () {
        audio.pause()
      }, false)
    }
  }
}
</script>

